






































export default

  props:
    ['sprite']

  computed:
    viewBoxSize: () ->
      height = Math.max(this.sprite.rgb_array.length * 10, this.sprite.rgb_array[0].length * 10)
      width = height
      return "0 0 #{width} #{height}"

  methods:
    computeColor: (pixel)->
      if pixel[3] != 0
        "rgb(#{pixel[0]},#{pixel[1]},#{pixel[2]})"
      else
        "rgba(0, 0, 0, 0)"



