











import Sprite from "./sprite"
import NewSprite from "./newSprite"

export default

  components:
    "sprite": Sprite
    "new-sprite": NewSprite

  data: () ->
    sprites: []

  methods:
    fetchSprites: () ->
      url = "/api/sprites/"
      options=
        headers:
          "accept": "application/json"
      component = this
      fetch(url)
      .catch((error) ->
        console.log error)
      .then((response) ->
        console.log response
        response.json()
        )
      .then((json) ->
        console.log json
        component.sprites = json
        )

  mounted: () ->
    this.fetchSprites()


